  width: 100%;
  margin-right:0px;
}


}


  
/*shop page styles*/

#page-header{
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_9___});
  width: 100%;
  height: 40vh;
  background-size: cover;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  padding: 14px;
}

#page-header h2,
#page-header p{
  color: #fff;
}

#pagination{
  text-align: center;
}

#pagination a{
  text-decoration: none;
  background-color: skyblue;
  padding: 15px 20px;
  border-radius: 4px;
  color: #fff;
  font-weight: 600;
}

#pagination a i{
  font-size: 16px;
  font-weight: 600;
}

/*single product*/

#prodetails {
  display:flex;
  margin-top:20px;
}

#prodetails .single-pro-image{
  width: 40%;
  margin-right:50px;
}

.small-img-group {
  display:flex;
  justify-content:space-between;
  
}
.small-img-col{
  flex-basis:24%;
  cursor: pointer;
}

#prodetails .single-pro-details {
  width: 50%;
  padding-top: 30%;

}

#prodetails .single-pro-details h4{
  padding: 40px 0 20px 0;
}
#prodetails .single-pro-details h2{
 font-size: 26px;
}
#prodetails .single-pro-details select{
  display: block;
  padding: 5px 10px;
  margin-bottom: 10px;
}
#prodetails .single-pro-details input{
  width: 50px;
  height: 47px;
  padding-right: 10px;
  font-size: 10px;
  margin-right: 10px;
}


#prodetails .single-pro-details input:focus{
  outline:none;

}

#prodetails .single-pro-details select:focus{
  outline:none;
}


#prodetails .single-pro-details button{
  background-color: #ec544e;
  color: #e3e6f3;
}

#prodetails .single-pro-details span{
  line-height: 25px;
}

/*about page*/

#about-head{
flex-direction:column;
}
  
#about-head img {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}

#about-head div {
  padding-left: 0px;
}
#about-head .video{
  width: 100%;
}
